import React, { useState } from 'react';
import './Store.css';
import IconSwitch from './IconSwitch/IconSwitch';
import CardsView from './CardsView/CardsView';
import ListView from './ListView/ListView';

const Store = () => {
    const [iconName, setIconName] = useState('view_list');
    const [cards, setCards] = useState(
        [
            {
                name: "Nike Metcon 2",
                price: "130",
                color: "red",
                img: "https://raw.githubusercontent.com/netology-code/ra16-homeworks/master/events-state/layouts/img/1.jpg"
            },
            {
                name: "Nike Metcon 2",
                price: "130",
                color: "green",
                img: "https://raw.githubusercontent.com/netology-code/ra16-homeworks/master/events-state/layouts/img/2.jpg"
            },
            {
                name: "Nike Metcon 2",
                price: "130",
                color: "blue",
                img: "https://raw.githubusercontent.com/netology-code/ra16-homeworks/master/events-state/layouts/img/3.jpg"
            },
            {
                name: "Nike Metcon 2",
                price: "130",
                color: "black",
                img: "https://raw.githubusercontent.com/netology-code/ra16-homeworks/master/events-state/layouts/img/4.jpg"
            },
            {
                name: "Nike free run",
                price: "170",
                color: "black",
                img: "https://raw.githubusercontent.com/netology-code/ra16-homeworks/master/events-state/layouts/img/7.jpg"
            },
            {
                name: "Nike Metcon 3",
                price: "150",
                color: "green",
                img: "https://raw.githubusercontent.com/netology-code/ra16-homeworks/master/events-state/layouts/img/5.jpg"
            }
        ]
    );

    const onSwitch = iconName => {
        setIconName(iconName === 'view_list' ? 'view_module' : 'view_list');
    };

    return (
        <>
            <div className="menu root__menu">
                <IconSwitch
                    iconName={ iconName }
                    onSwitch={ onSwitch }
                />
            </div>
            {
                iconName === 'view_list' ? <CardsView  cards={ cards } /> : <ListView cards={ cards } />
            }
        </>
    )
};

export default Store;

